const refreshPage = () => {
  Object.keys( decals ).forEach( key => delete decals[ key ] );
  loadDecals().then( loadedDecals => loadedDecals.slice(-config.maxDecals).forEach( ( d, i ) => decals[ i ] = d ) );
}

export default refreshPage;